import React from "react"
import Scale from './Scale'
import {totalStatics} from '../../utils/utils.js'

function Statistics(props) {
    const photoStat = ((props.data.statistics.photo / totalStatics) * 10).toFixed()
    const mapStat = ((props.data.statistics.map / totalStatics) * 10).toFixed()
    const factStat = ((props.data.statistics.quetion / totalStatics) * 10).toFixed()
    
    const resultStat = (Number(mapStat) + Number(photoStat))/2
    return (
      <div className="building__static">
          <div className="static__scales">
                <div className="static__scale">
                    <h2 className="static__title"></h2>
                    <Scale size={resultStat}/>
                </div>
                <div className="static__scale">
                    <h2 className="static__title"></h2>
                    <Scale size={factStat}/>
                </div>
          </div>
          <div className="static__description">
              <p className="description__title"></p>
              <p className="description__text">      ,
                          .
                     .    - 2022
                    .</p>
              <p className="description__info">     <span>{props.data.statistics.photo}</span>  <span>{totalStatics}</span></p>  
              <p className="description__info">   <span>{props.data.statistics.map}</span>  <span>{totalStatics}</span></p>  
              <p className="description__title"> </p>
              <p className="description__text">        ,      .</p>
              <p className="description__fact">,   ,  {props.data.statistics.fact}</p>
              <p className="description__info">      <span>{props.data.statistics.quetion}</span>  <span>{totalStatics}</span></p>  
          </div>
      </div>
    )
}

export default Statistics